.content {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: c;
  align-items: center;
  justify-content: center;
  background: url("https://eskipaper.com/images/flappy-bird-wallpaper-1.jpg")
    center no-repeat;
  background-size: cover;
  text-shadow: 1px 1px 1px #000;
}

.block {
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  border: 3px solid #7a670f;
  border-radius: 10px 0;
  background-color: #10081694;
}

.title {
  color: #fff;
  font-size: 55px;
}
.score {
  font-size: 44px;
  color: #fff;
  font-weight: 600;
}
.buttonBlock {
  margin: 10px;
  width: 300px;
  display: flex;
  justify-content: space-between;
}
.link {
  padding: 10px;
  border: 5px solid #ffffff;
  box-shadow: 0 0 1px 1px #000;
  border-radius: 5px;
  color: #fff;
  font-size: 18px;
  font-weight: 700;
  text-decoration: none;
  text-shadow: 1px 1px 1px #000;
  background-color: #f71e1efb;
  transition: 0.3s;
  cursor: pointer;
}

.link:hover {
  background-color: #ff1111;
  text-shadow: 3px 3px 1px #000;
  box-shadow: 0 3px 3px 3px #000;
}
.records {
  padding: 10px;
  width: 300px;
  box-shadow: 0 0 3px 3px;
  border-radius: 10px;
  list-style-type: none;
}

.recordsItem {
  border-bottom: 1px solid #828285;
  font-size: 30px;
  color: #fff;
}
.place {
  margin-right: 10px;
}
.place::after {
  content: ":";
}
