.content {
  position: absolute;
  top: -100px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  right: 0;
  height: calc(100vh + 200px);
  width: 150px;
  user-select: none;
}

.item {
  width: 100%;
  height: 400px;
}

.itemTop {
  transform: rotate(180deg);
}
